import {useHttp} from '../../hooks/http.hook';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { v4 as uuidv4 } from 'uuid';
import { selectAll } from '../heroesFilters/filtersSlice';
import store from '../../store';


import { heroCreated } from '../heroesList/heroesSlice';


//    :
//       .   
//        + 
//       uiid
//  :
//      json    POST
// :
//  <option></option>    
//   

const HeroesAddForm = () => {

    const [heroName, setHeroName] = useState('')
    const [heroDescr, setHeroDescr] = useState('')
    const [heroElement, setHeroElement] = useState('')

    const {filtersLoadingStatus} = useSelector(state => state.filters)
    const dispatch = useDispatch()
    const {request} = useHttp()
    const filters = selectAll(store.getState())

    const onSumbitHandler = (e) => {
        e.preventDefault()

        const newHero = {
            id: uuidv4(),
            name: heroName,
            description: heroDescr,
            element: heroElement
        }

        request("http://localhost:3001/heroes", "POST", JSON.stringify(newHero))
        .then(res => console.log(res, ' '))
        .then(dispatch(heroCreated(newHero)))
        .catch(err => console.log(err))
    
        setHeroName('');
        setHeroDescr('');
        setHeroElement('');
    }

    const renderFilters = (filters, status) => {
        if (status === 'loading') {
            return <option> </option>
        } else if (status === 'error') {
            return <option> </option>
        }

        if (filters && filters.length > 0) {
            return filters.map(({name, label}) => {
                if (name === 'all') return

                return <option key={name} value={name}>{label}</option>
            })
        }
    }

    return (
        <form className="border p-4 shadow-lg rounded" onSubmit={onSumbitHandler}>
            <div className="mb-3">
                <label htmlFor="name" className="form-label fs-4">  </label>
                <input 
                    required
                    type="text" 
                    name="name" 
                    className="form-control" 
                    id="name" 
                    value={heroName}
                    onChange={(e) => setHeroName(e.target.value)}
                    placeholder="  ?"/>
            </div>

            <div className="mb-3">
                <label htmlFor="text" className="form-label fs-4"></label>
                <textarea
                    required
                    name="text" 
                    className="form-control" 
                    id="text" 
                    placeholder="  ?"
                    onChange={(e) => setHeroDescr(e.target.value)}
                    style={{"height": '130px'}}/>
            </div>

            <div className="mb-3">
                <label htmlFor="element" className="form-label">  </label>
                <select 
                    required
                    className="form-select" 
                    id="element"
                    onChange={(e) => setHeroElement(e.target.value)} 
                    name="element">
                    <option >  ...</option>
                    {renderFilters(filters, filtersLoadingStatus)}
                </select>
            </div>

            <button type="submit" className="btn btn-primary"></button>
        </form>
    )
}

export default HeroesAddForm;